.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#FBDB89), to(#f2ad82));
    background-image: -webkit-linear-gradient(left top, #FBDB89, #f2ad82);
    background-image: -o-linear-gradient(left top, #FBDB89, #f2ad82);
    background-image: linear-gradient(to right bottom, #FBDB89, #f2ad82);
    background-size: cover;
    background-repeat: no-repeat#f2ad82;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
}

@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}


