@import url('https://fonts.googleapis.com/css2?family=Montserrat;&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@600&display=swap');

.Burger {
    width: 100%;
    margin: auto;
    height: 250px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    font-family: 'Nunito', Helvetica;
}

.text {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#FBDB89), to(#F48982));
    background-image: -webkit-linear-gradient(left top, #FBDB89, #F48982);
    background-image: -o-linear-gradient(left top, #FBDB89, #F48982);
    background-image: linear-gradient(to right bottom, #FBDB89, #F48982);
    -webkit-background-clip: text;
    color: transparent;
}

@media (min-width: 500px) and (min-height: 400px) {
    .Burger {
        width: 350px;
        height: 300px;
    }
}

@media (min-width: 500px) and (min-height: 401px) {
    .Burger {
        width: 450px;
        height: 400px;
    }
}

@media (min-width: 1000px) and (min-height: 700px) {
    .Burger {
        width: 700px;
        height: 400px;
    }
}



